import React, { useEffect, useState } from 'react';
import { Button } from '@/components/ui/button';
import { Shield, Plus, Edit } from 'lucide-react';
import { MasterPinDialog } from '@/components/MasterPinDialog';
import { MasterPinService } from '@/utils/masterPinService';
import { useToast } from '@/hooks/use-toast';
import PasswordConfirmDialog from '@/components/PasswordConfirmDialog';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';

interface MasterPinManagementProps {
  userId?: string;
}

export const MasterPinManagement: React.FC<MasterPinManagementProps> = ({
  userId
}) => {
  const { user } = useFirebaseAuth();
  const effectiveUserId = userId ?? user?.uid;
  const [masterPinDialogOpen, setMasterPinDialogOpen] = useState(false);
  const [masterPinMode, setMasterPinMode] = useState<'verify' | 'create' | 'change'>('create');
  const [isPasswordConfirmOpen, setIsPasswordConfirmOpen] = useState(false);
  const { toast } = useToast();

  const [hasMasterPin, setHasMasterPin] = useState<boolean>(false);

  useEffect(() => {
    let mounted = true;
    (async () => {
      try {
        const has = await MasterPinService.hasMasterPin(effectiveUserId);
        if (mounted) setHasMasterPin(!!has);
      } catch {
        if (mounted) setHasMasterPin(false);
      }
    })();
    return () => { mounted = false; };
  }, [effectiveUserId]);

  const getChangeCountKey = () => effectiveUserId ? `master_pin_change_count_${effectiveUserId}` : 'master_pin_change_count';
  const getChangeCount = () => {
    try {
      const raw = localStorage.getItem(getChangeCountKey());
      return raw ? parseInt(raw, 10) : 0;
    } catch {
      return 0;
    }
  };
  const incrementChangeCount = () => {
    try {
      const count = getChangeCount() + 1;
      localStorage.setItem(getChangeCountKey(), String(count));
    } catch {}
  };

  const handleCreateOrChangeMasterPin = async () => {
    try {
      const latestHas = await MasterPinService.hasMasterPin(effectiveUserId);
      setHasMasterPin(!!latestHas);
      setMasterPinMode(latestHas ? 'change' : 'create');
      setMasterPinDialogOpen(true);
    } catch {
      setMasterPinMode('create');
      setMasterPinDialogOpen(true);
    }
  };

  const handleMasterPinSuccess = () => {
    setMasterPinDialogOpen(false);
    if (masterPinMode === 'change') {
      incrementChangeCount();
    }
    // بعد الإنشاء الأول، اعتبر أن هناك رقم سري محفوظ لطلب الحالي في المرات القادمة
    if (masterPinMode === 'create') {
      setHasMasterPin(true);
    }
    toast({
      title: "تم بنجاح",
      description: hasMasterPin ? "تم تغيير الرقم السري الرئيسي" : "تم إنشاء الرقم السري الرئيسي",
    });
  };

  const handleMasterPinCancel = () => {
    setMasterPinDialogOpen(false);
  };

  return (
    <>
      <Button
        variant="outline"
        size="sm"
        onClick={handleCreateOrChangeMasterPin}
        className="flex items-center gap-2 text-sm"
      >
        <Shield className="h-4 w-4" />
        {hasMasterPin ? (
          <>
            <Edit className="h-4 w-4" />
            تغيير الرقم السري الرئيسي
          </>
        ) : (
          <>
            <Plus className="h-4 w-4" />
            إنشاء رقم سري رئيسي
          </>
        )}
      </Button>


      <MasterPinDialog
        open={masterPinDialogOpen}
        onOpenChange={setMasterPinDialogOpen}
        mode={masterPinMode}
        onSuccess={handleMasterPinSuccess}
        onCancel={handleMasterPinCancel}
      />
    </>
  );
};