import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from '@/components/ui/dialog';
import { ArrowLeft, Save, User, Crown, Calendar, Clock, RefreshCw, Shield, Type, Minus, Plus, Send, Moon, Sun } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import { useToast } from '@/hooks/use-toast';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { useFontSize } from '@/contexts/FontSizeContext';
import AvatarSelector from '@/components/AvatarSelector';
import { getAppDownloads, type AppDownloads } from '@/utils/appDownloadService';
import PinModal from '@/components/PinModal';
import { MasterPinManagement } from '@/components/MasterPinManagement';
import { formatDate, getDaysRemaining, getPlanDisplayName, getSubscriptionStatusColor, SubscriptionPlan, getUserSubscription } from '@/utils/subscriptionService';
import { Switch } from '@/components/ui/switch';
import { MasterPinDialog } from '@/components/MasterPinDialog';
import { openUssdDeepLink, sendTransferDeepLink, getUssdCode, getUssdCodeWithPin, getUssdCodeBySender } from '@/utils/ussdDialer';
import { walletSelectionPinService } from '@/utils/walletSelectionPinService';
import { userDataService } from '@/integrations/firebase/services';
import { useTheme } from 'next-themes';
import { Capacitor } from '@capacitor/core';
import { AppLauncher } from '@capacitor/app-launcher';
import { db, functions } from '@/firebase';
import { httpsCallable } from 'firebase/functions';
import { doc, setDoc } from 'firebase/firestore';

const ProfileSettingsPage: React.FC = () => {
  const navigate = useNavigate();
  const { toast } = useToast();
  const { user, profile, updateUserProfile, updateAvatarId, refreshProfile, userSubscription, isSubscriptionActive, checkSubscriptionStatus } = useFirebaseAuth();
  const { fontSize, setFontSize, isMobile } = useFontSize();
  const [appDownloads, setAppDownloads] = useState<AppDownloads | null>(null);
  
  const [formData, setFormData] = useState({
    fullName: profile?.fullName || '',
    phone: profile?.phone || '',
    sim1Number: (profile as any)?.sim1Number || '',
    sim2Number: (profile as any)?.sim2Number || '',
    avatarId: profile?.avatarId || ''
  });
  
  const [loading, setLoading] = useState(false);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [isDailyReportsLockEnabled, setIsDailyReportsLockEnabled] = useState<boolean>(false);
  const [isDailyLockTogglePinOpen, setIsDailyLockTogglePinOpen] = useState<boolean>(false);
  const [pendingDailyLockValue, setPendingDailyLockValue] = useState<boolean | null>(null);
  const [isWalletSelectionPinEnabled, setIsWalletSelectionPinEnabled] = useState<boolean>(false);
  const [isWalletSelectionPinToggleOpen, setIsWalletSelectionPinToggleOpen] = useState<boolean>(false);
  const [pendingWalletSelectionPinValue, setPendingWalletSelectionPinValue] = useState<boolean | null>(null);
  const [isDebtsPinRequired, setIsDebtsPinRequired] = useState<boolean>(() => {
    try {
      const key = user?.uid ? `debtsPinRequired_${user?.uid}` : 'debtsPinRequired';
      const v = localStorage.getItem(key) ?? localStorage.getItem('debtsPinRequired');
      return v === 'true';
    } catch { return false; }
  });
  const [isDebtsPinToggleOpen, setIsDebtsPinToggleOpen] = useState<boolean>(false);
  const [pendingDebtsPinValue, setPendingDebtsPinValue] = useState<boolean | null>(null);
  const isAndroidNative = Capacitor?.isNativePlatform && Capacitor.getPlatform() === 'android';
  const [isSimDialogOpen, setIsSimDialogOpen] = useState<boolean>(false);
  const [detectedSimNumbers, setDetectedSimNumbers] = useState<string[]>([]);
  const [manualSimInput, setManualSimInput] = useState<string>('');

  const normalizeMsisdnEgyptLocal = (raw?: string): string => {
    try {
      const s = String(raw || '').replace(/[^0-9+]/g, '');
      if (!s) return '';
      if (s.startsWith('+20')) {
        const rest = s.slice(3);
        return rest.startsWith('1') ? '0' + rest : rest;
      }
      if (s.startsWith('20')) {
        const rest = s.slice(2);
        return rest.startsWith('1') ? '0' + rest : rest;
      }
      if (s.startsWith('0')) return s;
      if (/^(10|11|12|15)/.test(s)) return '0' + s;
      return s;
    } catch { return ''; }
  };

  useEffect(() => {
    const onAndroidActiveSims = (ev: any) => {
      try {
        const sims: string[] = Array.isArray(ev?.detail?.sims) ? ev.detail.sims : [];
        const normalized = sims.map(s => normalizeMsisdnEgyptLocal(s)).filter(s => !!s);
        setDetectedSimNumbers(normalized);
        setIsSimDialogOpen(true);
      } catch { /* noop */ }
    };
    window.addEventListener('androidActiveSimNumbers', onAndroidActiveSims as any);
    return () => window.removeEventListener('androidActiveSimNumbers', onAndroidActiveSims as any);
  }, []);

  const triggerSimFetch = () => {
    const url = 'cashy://sim.fetch';
    let opened = false;
    try {
      if (Capacitor && Capacitor.getPlatform() !== 'web') {
        AppLauncher.openUrl({ url });
        opened = true;
      }
    } catch { /* noop */ }
    if (!opened) {
      try { window.location.href = url; } catch { /* noop */ }
    }
  };
  const [isReceiptNoteEnabled, setIsReceiptNoteEnabled] = useState<boolean>(() => {
    try {
      const key = user?.uid ? `receiptNoteEnabled_${user?.uid}` : 'receiptNoteEnabled';
      const v = localStorage.getItem(key) ?? localStorage.getItem('receiptNoteEnabled');
      return v === null ? true : v === 'true';
    } catch {
      return true;
    }
  });
  const [isWithdrawSenderNameInputEnabled, setIsWithdrawSenderNameInputEnabled] = useState<boolean>(() => {
    try {
      const key = user?.uid ? `withdrawSenderNameInputEnabled_${user?.uid}` : 'withdrawSenderNameInputEnabled';
      const v = localStorage.getItem(key) ?? localStorage.getItem('withdrawSenderNameInputEnabled');
      return v === null ? false : v === 'true';
    } catch {
      return false;
    }
  });
  const [isSmsReadingEnabled, setIsSmsReadingEnabled] = useState<boolean>(() => {
    try {
      const v = (profile as any)?.smsReadingEnabled;
      return v === undefined ? true : !!v;
    } catch {
      return true;
    }
  });
  const [isSmsAutoRegisterEnabled, setIsSmsAutoRegisterEnabled] = useState<boolean>(() => {
    try {
      const v = (profile as any)?.smsAutoRegisterEnabled;
      return v === undefined ? false : !!v;
    } catch {
      return false;
    }
  });
  const { theme, setTheme } = useTheme();
  const [isDark, setIsDark] = useState<boolean>(false);

  useEffect(() => {
    try {
      const t = theme || (localStorage.getItem('theme') || 'system');
      const prefersDark = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;
      setIsDark(t === 'dark' || (t === 'system' && prefersDark));
    } catch {}
  }, [theme]);

  useEffect(() => {
    (async () => {
      try {
        const uid = user?.uid || '';
        if (!uid) return;
        const data = await userDataService.getUserData(uid);
        const remote: any = (data as any)?.withdrawSenderNameInputEnabled;
        if (typeof remote === 'boolean') {
          setIsWithdrawSenderNameInputEnabled(remote);
          try { localStorage.setItem(`withdrawSenderNameInputEnabled_${uid}`, remote ? 'true' : 'false'); } catch {}
        }
      } catch {}
    })();
  }, [user?.uid]);

  const handleReceiptNoteToggle = (nextVal: boolean) => {
    setIsReceiptNoteEnabled(nextVal);
    try {
      if (user?.uid) {
        localStorage.setItem(`receiptNoteEnabled_${user.uid}`, nextVal ? 'true' : 'false');
      }
      localStorage.setItem('receiptNoteEnabled', nextVal ? 'true' : 'false');
    } catch {}
    toast({
      title: nextVal ? 'ستظهر ملاحظة الإيصال' : 'سيتم إخفاء ملاحظة الإيصال',
      description: nextVal ? 'سيتم عرض الملاحظة عند توفرها.' : 'لن يتم عرض الملاحظة داخل الإيصال والطباعة.',
    });
  };
  const handleWithdrawSenderNameInputToggle = (nextVal: boolean) => {
    setIsWithdrawSenderNameInputEnabled(nextVal);
    try {
      if (user?.uid) {
        localStorage.setItem(`withdrawSenderNameInputEnabled_${user.uid}`, nextVal ? 'true' : 'false');
        userDataService.updateUserData(user.uid, { withdrawSenderNameInputEnabled: nextVal });
      }
      localStorage.setItem('withdrawSenderNameInputEnabled', nextVal ? 'true' : 'false');
    } catch {}
    toast({
      title: nextVal ? 'سيظهر خانة اسم الراسل' : 'سيتم إخفاء خانة اسم الراسل',
      description: nextVal ? 'ستظهر خانة كتابة اسم الراسل في نموذج السحب.' : 'لن تظهر خانة كتابة اسم الراسل في نموذج السحب.',
    });
  };
  const handleSmsReadingToggle = async (nextVal: boolean) => {
    setIsSmsReadingEnabled(nextVal);
    try {
      await updateUserProfile({ smsReadingEnabled: nextVal });
    } catch {}
    toast({
      title: nextVal ? 'تم تفعيل قراءة رسائل SMS' : 'تم إلغاء قراءة رسائل SMS',
      description: nextVal ? 'ستظهر النافذة المنبثقة عند استقبال رسالة.' : 'لن تتم قراءة الرسائل ولن تظهر النافذة المنبثقة.',
    });
  };
  const handleSmsAutoRegisterToggle = async (nextVal: boolean) => {
    setIsSmsAutoRegisterEnabled(nextVal);
    try {
      await updateUserProfile({ smsAutoRegisterEnabled: nextVal });
    } catch {}
    toast({
      title: nextVal ? 'تفعيل التسجيل التلقائي بدون نافذة' : 'إلغاء التسجيل التلقائي بدون نافذة',
      description: nextVal ? 'سيتم حفظ المعاملة تلقائياً بدون عرض النافذة المنبثقة.' : 'ستظهر النافذة وسيحتاج تأكيد التسجيل يدوياً.',
    });
  };
  // 🔹 أجهزة ADB المتصلة واختيار الجهاز الافتراضي
  const [adbDevices, setAdbDevices] = useState<Array<{ id: string; model?: string; name?: string; type?: string }>>([]);
  const [selectedDevice, setSelectedDevice] = useState<string>(() => {
    try { return localStorage.getItem('cashySelectedDevice') || ''; } catch { return ''; }
  });
  const [deviceStatus, setDeviceStatus] = useState<string>('');
  const [wifiIp, setWifiIp] = useState<string>(() => {
    try { return localStorage.getItem('cashyWifiIp') || ''; } catch { return ''; }
  });
  const [telegramChatId, setTelegramChatId] = useState<string>('');
  const [isZeroPlan, setIsZeroPlan] = useState<boolean>(false);
  // قائمة الأجهزة المعروفة (المضافة مسبقًا)
  const KNOWN_DEVICES_KEY = 'cashyKnownDevices';
  const getKnownDevices = (): string[] => {
    try {
      const raw = localStorage.getItem(KNOWN_DEVICES_KEY);
      const arr = raw ? JSON.parse(raw) : [];
      return Array.isArray(arr) ? arr.filter((x) => typeof x === 'string') : [];
    } catch { return []; }
  };
  const addKnownDevice = (id: string) => {
    try {
      const set = new Set(getKnownDevices());
      set.add(id);
      localStorage.setItem(KNOWN_DEVICES_KEY, JSON.stringify(Array.from(set)));
    } catch {}
  };
  
  
  // 🔹 إعدادات التحويل عبر USSD + نافذة إدخال PIN
  const [receiver, setReceiver] = useState<string>('');
  const [amount, setAmount] = useState<string>('');
  const [pinOpen, setPinOpen] = useState<boolean>(false);
  const [transferStatus, setTransferStatus] = useState<string>('');
  // 🔹 إعدادات Cashy Bridge (رابط الجسر + معرف الجهاز)
  const [bridgeLink, setBridgeLink] = useState<string>(() => String((profile as any)?.bridgeLink || ''));
  const [bridgeDeviceId, setBridgeDeviceId] = useState<string>(() => String((profile as any)?.bridgeDeviceId || ''));

  // إظهار أدوات الجهاز (إدخال IP والاتصال عبر الواي فاي) داخل تطبيق الكمبيوتر فقط
  const SHOW_DEVICE_UTILS = (() => {
    const isElectron = typeof window !== 'undefined' && (
      (window as any).electronAPI ||
      window.location.protocol === 'file:' ||
      (typeof navigator !== 'undefined' && navigator.userAgent.toLowerCase().includes('electron'))
    );
    return isElectron && !isMobile;
  })();
  const SHOW_TRANSFER_UI = false;


  // استرجاع رقم محفظة المُرسل المختارة من التخزين المحلي
  const getSelectedWalletPhone = (): string => {
    try {
      const selectedKey = `selectedWallet_${user?.uid || 'default'}`;
      const walletsKey = `wallets_${user?.uid || 'default'}`;
      const selectedId = localStorage.getItem(selectedKey);
      const raw = localStorage.getItem(walletsKey);
      const list = raw ? JSON.parse(raw) : [];
      if (selectedId && Array.isArray(list)) {
        const w = list.find((x: any) => x && x.id === selectedId);
        return (w?.phone || '').toString();
      }
      return '';
    } catch {
      return '';
    }
  };

  useEffect(() => {
    // مزامنة الحالة عندما يتم تحديث بروفايل المستخدم بعد التحميل
    setBridgeLink(String((profile as any)?.bridgeLink || ''));
    setBridgeDeviceId(String((profile as any)?.bridgeDeviceId || ''));
  }, [profile?.bridgeLink, profile?.bridgeDeviceId]);

  useEffect(() => {
    try {
      const v = (profile as any)?.smsReadingEnabled;
      setIsSmsReadingEnabled(v === undefined ? true : !!v);
    } catch {}
  }, [profile]);
  useEffect(() => {
    try {
      const v = (profile as any)?.smsAutoRegisterEnabled;
      setIsSmsAutoRegisterEnabled(v === undefined ? false : !!v);
    } catch {}
  }, [profile]);

  const handleConnectWifi = async () => {
    try {
      const api: any = (window as any).electronAPI;
      if (!api || typeof api.connectWifi !== 'function') {
        setDeviceStatus('❌ هذه الميزة متاحة داخل تطبيق سطح المكتب فقط');
        return;
      }
      if (!wifiIp.trim()) {
        setDeviceStatus('❌ أدخل عنوان IP مع المنفذ (مثال: 192.168.1.8:5555)');
        return;
      }
      setDeviceStatus('🔄 جارِ الاتصال عبر Wi‑Fi...');
      const res = await api.connectWifi(wifiIp.trim());
      setDeviceStatus(`✅ تم الاتصال: ${String(res || '').trim() || 'نجاح'}`);
      // أضف الجهاز إلى القائمة المعروفة ليظهر في الاختيار لاحقًا
      addKnownDevice(wifiIp.trim());
      // سيظهر الجهاز تلقائياً في القائمة خلال ثوانٍ
    } catch (err: any) {
      setDeviceStatus(`❌ فشل الاتصال: ${err?.message || String(err)}`);
    }
  };

  useEffect(() => {
    try { localStorage.setItem('cashyWifiIp', wifiIp); } catch {}
  }, [wifiIp]);

  useEffect(() => {
    (async () => {
      try {
        const uid = user?.uid || '';
        if (!uid) return;
        const data = await userDataService.getUserData(uid);
        const val = String((data as any)?.telegramChatId || '').trim();
        setTelegramChatId(val);
      } catch {}
    })();
  }, [user?.uid]);

  useEffect(() => {
    const api: any = (window as any).electronAPI;
    if (api && typeof api.onAdbDevices === 'function') {
      api.onAdbDevices((list: Array<{ id: string; model?: string; name?: string; type?: string }>) => {
        const devices = Array.isArray(list) ? list : [];
        // أظهر جميع الأجهزة (USB و Wi‑Fi) بدون تصفية
        const filtered = devices;
        setAdbDevices(filtered);
        // حافظ على الاختيار إن كان موجوداً، وإلا اختر أول جهاز
        const saved = (() => { try { return localStorage.getItem('cashySelectedDevice') || ''; } catch { return ''; } })();
        const existsExact = saved && filtered.some(d => d.id === saved);
        if (existsExact) {
          setSelectedDevice(saved);
        } else {
          // محاولة التحويل تلقائياً من USB إلى Wi‑Fi لنفس الجهاز عند فصل الكابل
          const base = saved ? String(saved).split(':')[0] : '';
          const wifiCandidate = base
            ? filtered.find(d => (d.id.startsWith(base) || d.id.includes(base)) && String(d.type).toLowerCase().includes('wifi'))
            : undefined;
          if (wifiCandidate?.id) {
            setSelectedDevice(wifiCandidate.id);
          } else if (!selectedDevice && filtered[0]?.id) {
            setSelectedDevice(filtered[0].id);
          }
        }
      });
    }
  }, []);

  

  useEffect(() => {
    try {
      if (selectedDevice) localStorage.setItem('cashySelectedDevice', selectedDevice);
    } catch {}
  }, [selectedDevice]);

  useEffect(() => {
    try {
      const key = user?.uid ? `dailyReportsLockEnabled_${user.uid}` : 'dailyReportsLockEnabled';
      const v = localStorage.getItem(key) ?? localStorage.getItem('dailyReportsLockEnabled');
      setIsDailyReportsLockEnabled(v === 'true');
    } catch {}
  }, [user?.uid]);

  // تحميل حالة طلب الرقم السري عند اختيار المحفظة
  useEffect(() => {
    try {
      setIsWalletSelectionPinEnabled(walletSelectionPinService.isEnabled(user?.uid));
    } catch {}
  }, [user?.uid]);

  const handleDailyLockToggle = (next: boolean) => {
    setPendingDailyLockValue(next);
    setIsDailyLockTogglePinOpen(true);
  };

  const handleWalletSelectionPinToggle = (next: boolean) => {
    setPendingWalletSelectionPinValue(next);
    setIsWalletSelectionPinToggleOpen(true);
  };

  const handleDebtsPinToggle = (next: boolean) => {
    setPendingDebtsPinValue(next);
    setIsDebtsPinToggleOpen(true);
  };

  // تحديث بيانات الاشتراك عند تحميل الصفحة
  useEffect(() => {
    const refreshSubscriptionData = async () => {
      try {
        await checkSubscriptionStatus();
        console.log('تم تحديث بيانات الاشتراك عند تحميل صفحة البروفايل');
      } catch (error) {
        console.error('خطأ في تحديث بيانات الاشتراك:', error);
      }
    };

    refreshSubscriptionData();
  }, [checkSubscriptionStatus]);

  useEffect(() => {
    (async () => {
      try {
        let zero = false;
        const rawLocalPlan: any = (userSubscription as any)?.subscription?.planType ?? (userSubscription as any)?.planType ?? null;
        const planLocalStr = typeof rawLocalPlan === 'string' ? rawLocalPlan.toLowerCase() : null;
        if (rawLocalPlan === SubscriptionPlan.ZERO || planLocalStr === 'zero' || rawLocalPlan === 0 || planLocalStr === '0') {
          zero = true;
        } else if (user?.uid) {
          const remoteSub: any = await getUserSubscription(user.uid);
          const rawRemotePlan: any = remoteSub?.planType ?? remoteSub?.plan ?? null;
          const planRemoteStr = typeof rawRemotePlan === 'string' ? rawRemotePlan.toLowerCase() : null;
          if (rawRemotePlan === SubscriptionPlan.ZERO || planRemoteStr === 'zero' || rawRemotePlan === 0 || planRemoteStr === '0') zero = true;
        }
        setIsZeroPlan(zero);
      } catch { setIsZeroPlan(false); }
    })();
  }, [user?.uid, (userSubscription as any)?.subscription?.planType, (userSubscription as any)?.planType]);

  // تحميل معلومات النسخة وروابط التطبيق لعرض رقم النسخة في البروفيل
  useEffect(() => {
    const loadDownloads = async () => {
      try {
        const data = await getAppDownloads();
        setAppDownloads(data);
      } catch (e) {
        console.warn('تعذّر تحميل معلومات النسخة', e);
      }
    };
    loadDownloads();
  }, []);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleAvatarSelect = (avatarId: string) => {
    setFormData(prev => ({
      ...prev,
      avatarId
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      // حفظ أرقام الشرائح محليًا فقط (لكل جهاز)
      try {
        const uid = user?.uid || 'default';
        const s1 = String(formData.sim1Number || '').trim();
        const s2 = String(formData.sim2Number || '').trim();
        localStorage.setItem(`sim1Number_${uid}`, s1);
        localStorage.setItem(`sim2Number_${uid}`, s2);
        localStorage.setItem('sim1Number', s1);
        localStorage.setItem('sim2Number', s2);
        try {
          const { Preferences } = await import('@capacitor/preferences');
          await Preferences.set({ key: 'sim1_number', value: s1 });
          await Preferences.set({ key: 'sim2_number', value: s2 });
        } catch {}
      } catch {}

      // تحديث باقي بيانات البروفايل بدون أرقام الشرائح
      await updateUserProfile({
        fullName: formData.fullName,
        phone: formData.phone,
      });

      // تحديث صورة البروفيل إذا تم تغييرها
      if (formData.avatarId !== profile?.avatarId) {
        await updateAvatarId(formData.avatarId);
      }

      if (user?.uid) {
        try {
          await userDataService.updateUserData(user.uid, {
            telegramChatId: isZeroPlan ? null : (telegramChatId.trim() || null)
          });
        } catch {}
      }
      await refreshProfile();

      toast({
        title: "تم التحديث بنجاح",
        description: "تم حفظ الإعدادات محليًا لهذا الجهاز",
      });

      navigate('/user/dashboard', { replace: true });
    } catch (error) {
      console.error('Error updating profile:', error);
      toast({
        title: "خطأ في التحديث",
        description: "حدث خطأ أثناء حفظ الإعدادات",
        variant: "destructive",
      });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    try {
      const uid = user?.uid || 'default';
      const s1 = (localStorage.getItem(`sim1Number_${uid}`) ?? localStorage.getItem('sim1Number') ?? '') || '';
      const s2 = (localStorage.getItem(`sim2Number_${uid}`) ?? localStorage.getItem('sim2Number') ?? '') || '';
      setFormData(prev => ({ ...prev, sim1Number: s1, sim2Number: s2 }));
    } catch {}
  }, [user?.uid]);

  const handleRefreshSubscription = async () => {
    setIsRefreshing(true);
    try {
      // تحديث البروفايل وبيانات الاشتراك معاً
      await refreshProfile();
      await checkSubscriptionStatus();
      
      console.log('تم تحديث بيانات الاشتراك يدوياً:', {
        userSubscription,
        isSubscriptionActive
      });
      
      toast({
        title: "تم تحديث بيانات الاشتراك",
        description: "تم تحديث معلومات اشتراكك بنجاح",
      });
    } catch (error) {
      console.error('Error refreshing subscription:', error);
      toast({
        title: "خطأ في التحديث",
        description: "حدث خطأ أثناء تحديث بيانات الاشتراك",
        variant: "destructive",
      });
    } finally {
      setIsRefreshing(false);
    }
  };

  return (
    <div className="min-h-[100dvh] p-4 relative bg-gray-50 bg-[radial-gradient(140%_140%_at_10%_10%,#f3f4f6_0%,#e5e7eb_45%,#d1d5db_100%)]">
      <div className="max-w-2xl mx-auto">
        {/* Header */}
        <div className="flex items-center gap-4 mb-6">
          <Button
            variant="outline"
            size="sm"
            onClick={() => navigate('/user/dashboard', { replace: true })}
            className="flex items-center gap-2"
          >
            <ArrowLeft className="h-4 w-4" />
            العودة
          </Button>
          <h1 className="text-2xl font-bold text-gray-900">إعدادات البروفيل</h1>
        </div>

        {/* Theme Toggle */}
        <Card className="shadow-lg mb-6 bg-white border border-gray-200">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              {isDark ? <Moon className="h-5 w-5" /> : <Sun className="h-5 w-5" />}
              الوضع الليلي
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center justify-between">
              <p className="text-sm text-gray-600">تفعيل/إلغاء الوضع الليلي للتطبيق</p>
              <Button
                type="button"
                onClick={() => {
                  const next = !isDark;
                  setIsDark(next);
                  const nextTheme = next ? 'dark' : 'light';
                  try { localStorage.setItem('theme', nextTheme); } catch {}
                  setTheme(nextTheme);
                  try {
                    if (user?.uid) {
                      setDoc(doc(db, 'userSettings', user.uid), { darkMode: nextTheme }, { merge: true });
                    }
                  } catch {}
                  toast({ title: next ? 'تم تفعيل الوضع الليلي' : 'تم إلغاء الوضع الليلي' });
                }}
                className={isDark ? "px-4 bg-slate-800 text-white hover:bg-slate-700" : "px-4 bg-black text-white hover:bg-gray-800"}
              >
                {isDark ? 'إلغاء الوضع الليلي' : 'تفعيل الوضع الليلي'}
              </Button>
            </div>
          </CardContent>
        </Card>

        {/* App Version Badge */}
        <div className="mb-4">
          {(() => {
            const isElectron = typeof window !== 'undefined' && (
              (window as any).electronAPI ||
              window.location.protocol === 'file:' ||
              (typeof navigator !== 'undefined' && navigator.userAgent.toLowerCase().includes('electron'))
            );
            const [runtimeDesktopVersion, setRuntimeDesktopVersion] = React.useState<string | undefined>(undefined);
            React.useEffect(() => {
              try {
                const api: any = (window as any).electronAPI;
                if (api && typeof api.appVersion === 'function') {
                  api.appVersion().then((v: any) => {
                    if (typeof v === 'string' && !v.startsWith('err:')) setRuntimeDesktopVersion(v);
                  }).catch(() => {});
                }
              } catch {}
            }, []);
            const version = (() => {
              const localVersion = (import.meta as any)?.env?.VITE_APP_VERSION as string | undefined;
              const isElectronEnv = typeof window !== 'undefined' && ((window as any).electronAPI || window.location.protocol === 'file:' || (typeof navigator !== 'undefined' && navigator.userAgent.toLowerCase().includes('electron')));
              if (isElectronEnv && (profile as any)?.desktopInstalledVersion) return (profile as any)?.desktopInstalledVersion;
              if (isElectronEnv && runtimeDesktopVersion) return runtimeDesktopVersion;
              if (localVersion) return localVersion;
              if (!appDownloads && !profile) return undefined;
              if (isMobile) return (profile as any)?.androidInstalledVersion || (profile as any)?.lastAckAndroidVersion || appDownloads?.androidVersion || appDownloads?.pcVersion;
              if (isElectronEnv) return appDownloads?.pcVersion || appDownloads?.androidVersion;
              return appDownloads?.pcVersion || appDownloads?.androidVersion;
            })();
            const label = isMobile ? 'نسخة التطبيق (موبايل)' : 'نسخة التطبيق (كمبيوتر)';
            return (
              <div className="inline-flex items-center gap-2 rounded-lg border border-indigo-200 bg-indigo-50 px-3 py-2 text-sm text-indigo-700">
                <span className="font-semibold">{label}:</span>
                <span className="rounded-md bg-white px-2 py-1 border border-indigo-200">
                  {version ? `v${version}` : 'غير محدد'}
                </span>
              </div>
            );
          })()}
        </div>

        {/* Profile Settings Card */}
      <Card className="shadow-lg mb-6 bg-white border border-gray-200">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <User className="h-5 w-5" />
              تحديث بيانات البروفيل
            </CardTitle>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-6">
              {/* Avatar Selection */}
              <div className="space-y-2">
                <Label>صورة البروفيل</Label>
                <div className="flex justify-center">
                  <AvatarSelector
                    selectedAvatarId={formData.avatarId}
                    onAvatarSelect={handleAvatarSelect}
                  />
                </div>
              </div>

              {/* Full Name */}
              <div className="space-y-2">
                <Label htmlFor="fullName">الاسم الكامل</Label>
                <Input
                  id="fullName"
                  name="fullName"
                  type="text"
                  value={formData.fullName}
                  onChange={handleInputChange}
                  placeholder="أدخل اسمك الكامل"
                  className="text-right"
                />
              </div>

              {/* Phone */}
              <div className="space-y-2">
                <Label htmlFor="phone">رقم الهاتف</Label>
              <Input
                id="phone"
                name="phone"
                type="tel"
                value={formData.phone}
                onChange={handleInputChange}
                placeholder="أدخل رقم هاتفك"
                className="text-right"
              />
            </div>

            {isAndroidNative && (
              <>
                <div className="space-y-2">
                  <Label>أرقام الشرائح</Label>
                  <div className="flex flex-wrap gap-2">
                    <Button type="button" onClick={triggerSimFetch}>
                      📱 جلب رقم الشريحة من الهاتف
                    </Button>
                    <Button type="button" variant="secondary" onClick={() => setIsSimDialogOpen(true)}>
                      ✍️ إدخال يدوي
                    </Button>
                  </div>
                  {formData.sim1Number ? (
                    <div className="text-sm text-muted-foreground text-right">
                      الشريحة المختارة: {formData.sim1Number}
                    </div>
                  ) : (
                    <div className="text-sm text-muted-foreground text-right">
                      لم يتم اختيار رقم شريحة بعد
                    </div>
                  )}
                </div>

                <Dialog open={isSimDialogOpen} onOpenChange={setIsSimDialogOpen}>
                  <DialogContent className="sm:max-w-md">
                    <DialogHeader>
                      <DialogTitle>اختر رقم الشريحة</DialogTitle>
                    </DialogHeader>
                    <div className="space-y-3">
                      {detectedSimNumbers.length > 0 && (
                        <div className="space-y-2">
                          <Label>الأرقام المكتشفة من الهاتف</Label>
                          <div className="flex flex-col gap-2">
                            {detectedSimNumbers.map((n, idx) => (
                              <Button
                                key={idx}
                                type="button"
                                variant="secondary"
                                onClick={async () => {
                                  const fixed = normalizeMsisdnEgyptLocal(n);
                                  setFormData(prev => ({ ...prev, sim1Number: fixed, sim2Number: '' }));
                                  try {
                                    await updateUserProfile({ sim1Number: fixed, sim2Number: null });
                                    const uid = user?.uid || '';
                                    if (uid) localStorage.setItem(`sim1Number_${uid}`, fixed);
                                    localStorage.setItem('sim1Number', fixed);
                                    try { localStorage.removeItem(`sim2Number_${uid}`); localStorage.removeItem('sim2Number'); } catch {}
                                    toast({ title: 'تم حفظ رقم الشريحة', description: fixed });
                                  } catch {}
                                  setIsSimDialogOpen(false);
                                }}
                                className="justify-between"
                              >
                                <span>{n}</span>
                                <span className="text-xs text-muted-foreground">اختر</span>
                              </Button>
                            ))}
                          </div>
                        </div>
                      )}
                      <div className="space-y-2">
                        <Label>إدخال يدوي</Label>
                        <Input
                          type="tel"
                          placeholder="أدخل رقم الشريحة"
                          value={manualSimInput}
                          onChange={(e) => setManualSimInput(e.target.value)}
                          className="text-right"
                        />
                      </div>
                    </div>
                    <DialogFooter className="sm:justify-end">
                      <Button
                        type="button"
                        onClick={async () => {
                          const fixed = normalizeMsisdnEgyptLocal(manualSimInput);
                          if (fixed) {
                            setFormData(prev => ({ ...prev, sim1Number: fixed, sim2Number: '' }));
                            try {
                              await updateUserProfile({ sim1Number: fixed, sim2Number: null });
                              const uid = user?.uid || '';
                              if (uid) localStorage.setItem(`sim1Number_${uid}`, fixed);
                              localStorage.setItem('sim1Number', fixed);
                              try { localStorage.removeItem(`sim2Number_${uid}`); localStorage.removeItem('sim2Number'); } catch {}
                              toast({ title: 'تم حفظ رقم الشريحة', description: fixed });
                            } catch {}
                          }
                          setIsSimDialogOpen(false);
                        }}
                      >
                        حفظ الرقم
                      </Button>
                    </DialogFooter>
                  </DialogContent>
                </Dialog>
              </>
            )}

            <div className="space-y-2">
              <Label htmlFor="telegramChatId">Telegram Chat ID</Label>
              <Input
                id="telegramChatId"
                name="telegramChatId"
                type="text"
                value={telegramChatId}
                onChange={(e) => { if (!isZeroPlan) setTelegramChatId(e.target.value); }}
                disabled={isZeroPlan}
                readOnly={isZeroPlan}
                placeholder="أدخل Chat ID"
                className="text-right"
              />
              
              
            </div>

              {/* Device Selection */}
              <div className="space-y-2">
                <Label>📱 اختر الجهاز المتصل</Label>
              <select
                value={selectedDevice}
                onChange={(e) => setSelectedDevice(e.target.value)}
                className="border rounded-md p-2 w-full text-right"
              >
                <option value="">— اختر جهاز —</option>
                {adbDevices.map((d, i) => (
                  <option key={i} value={d.id}>
                    {(d.model || d.name || 'Android')} ({d.type || 'USB/Wi‑Fi'}) — {d.id}
                  </option>
                ))}
              </select>
              {SHOW_DEVICE_UTILS && (
                <div className="flex gap-2">
                  <Button
                    type="button"
                    variant="default"
                    onClick={async () => {
                      try {
                        if (!selectedDevice) {
                          setDeviceStatus('❌ اختر جهاز أولاً');
                          return;
                        }
                        const api: any = (window as any).electronAPI;
                        if (!api || typeof api.sendUssd !== 'function') {
                          setDeviceStatus('❌ هذه الميزة متاحة داخل تطبيق سطح المكتب فقط');
                          return;
                        }
                        const testCode = getUssdCode('01000000000', 10) || '*9*7*01000000000*10#';
                        try {
                          await api.sendUssd(selectedDevice, testCode);
                        } catch (e1: any) {
                          const maybeIp = selectedDevice.includes(':') ? selectedDevice.split(':').slice(0,2).join(':') : '';
                          if (maybeIp && typeof api.connectWifi === 'function') {
                            await api.connectWifi(maybeIp);
                            await api.sendUssd(selectedDevice, testCode);
                          } else {
                            throw e1;
                          }
                        }
                        setDeviceStatus(`✅ تم إرسال كود اختبار إلى ${selectedDevice}`);
                      } catch (err) {
                        console.error('USSD test error:', err);
                        setDeviceStatus('❌ حدث خطأ أثناء الإرسال');
                      }
                    }}
                  >
                    📤 إرسال كود اختبار
                  </Button>
                  <Input
                    value={wifiIp}
                    onChange={(e) => setWifiIp(e.target.value)}
                    placeholder="IP:PORT — مثال: 192.168.1.8:5555"
                    className="flex-1 text-right"
                  />
                  <Button type="button" variant="outline" onClick={handleConnectWifi}>
                    🌐 اتصال عبر Wi‑Fi
                  </Button>
                </div>
              )}
              {/* 🔹 نموذج التحويل عبر USSD وفتح نافذة PIN */}
              {SHOW_TRANSFER_UI && (
              <div className="mt-4 p-3 bg-gray-50 rounded-lg border space-y-3">
                <div className="grid grid-cols-1 sm:grid-cols-3 gap-2">
                  <div className="flex items-center gap-2">
                    <Input
                      value={receiver}
                      onChange={(e) => setReceiver(e.target.value)}
                      placeholder="رقم المُستلم"
                      className="text-right flex-1"
                    />
                  </div>
                  <div className="flex items-center gap-2">
                    <Input
                      value={amount}
                      onChange={(e) => setAmount(e.target.value)}
                      placeholder="المبلغ"
                      className="text-right flex-1"
                    />
                  </div>
                  <Button
                    type="button"
                    className="w-full flex items-center gap-2"
                    onClick={async () => {
                      try {
                        setTransferStatus('');
                        const receiverDigits = String(receiver || '').replace(/\D/g, '');
                        const amountInt = Math.max(1, Math.round(Number(String(amount || '0').replace(/\D/g, '')) || 0));
                        const senderPhone = getSelectedWalletPhone();
                        if (!senderPhone) {
                          setTransferStatus('❌ يرجى اختيار محفظة المُرسل أولاً');
                          return;
                        }
                        const code = getUssdCodeBySender(senderPhone, receiverDigits, amountInt);
                        if (!code) {
                          setTransferStatus('❌ الشبكة غير مدعومة أو الرقم غير صحيح');
                          return;
                        }
                        const api: any = (window as any).electronAPI;
                        if (api && typeof api.sendUssd === 'function' && selectedDevice) {
                          try { /* عدم انتظار تحديث الأجهزة لتجنب التهنيج */ } catch {}
                          // سطح المكتب: إرسال عبر ADB مباشرة مع اختيار الشريحة تلقائيًا
                          try {
                            const pick = typeof api.pickSimAutomatically === 'function'
                              ? await api.pickSimAutomatically(selectedDevice, senderPhone)
                              : { deviceId: selectedDevice, slot: 0 };
                            const decoratedId = `${pick.deviceId}:sim${pick.slot}`;
                            await api.sendUssd(decoratedId, code);
                          } catch (e1: any) {
                            try {
                              // رجوع للطريقة التقليدية إذا فشل اختيار الشريحة
                              await api.sendUssd(selectedDevice, code);
                            } catch (e2: any) {
                              // محاولة اتصال تلقائي عبر Wi‑Fi ثم إعادة الإرسال إذا كانت الهوية على شكل IP:PORT
                              const maybeIp = selectedDevice.includes(':') ? selectedDevice.split(':').slice(0,2).join(':') : '';
                              if (maybeIp && typeof api.connectWifi === 'function') {
                                try {
                                  await api.connectWifi(maybeIp);
                                  await api.sendUssd(selectedDevice, code);
                                } catch (e3: any) {
                                  throw e3;
                                }
                              } else {
                                throw e2;
                              }
                            }
                          }
                          setTransferStatus(`✅ تم إرسال كود التحويل: ${code}`);
                          setPinOpen(true);
                        } else {
                          // الموبايل: إرسال الكود مباشرة من الجهاز عبر USSD
                          openUssdDeepLink(code);
                          setTransferStatus(`✅ تم إرسال كود التحويل مباشرة: ${code}`);
                          setPinOpen(true);
                        }
                      } catch (err: any) {
                        console.error('USSD send error:', err);
                        setTransferStatus(`❌ فشل الإرسال: ${err?.message || String(err)}`);
                        // فتح نصيحة سريعة: تأكد من تفعيل Wireless debugging واتصال adb connect
                        setDeviceStatus('❌ الجهاز غير متصل. جرّب الضغط على "اتصال عبر Wi‑Fi" أو فعّل Wireless debugging ثم قم بالاقتران عبر ADB.');
                      }
                    }}
                  >
                    <Send className="h-4 w-4" />
                    إرسال التحويل
                  </Button>
                </div>
                {transferStatus && (
                  <p className="text-center text-sm mt-2">{transferStatus}</p>
                )}
                <div className="text-xs text-gray-500 text-right">
                  بعد إرسال كود التحويل، ستظهر نافذة لإدخال الرقم السري ويتم إرساله للهاتف تلقائياً.
                </div>
              </div>
              )}
              {/* 🔹 إعدادات الجسر (ngrok) للموبايل */}
              {SHOW_TRANSFER_UI && (
              <div className="mt-4 p-3 bg-white rounded-lg border space-y-3">
                <p className="text-sm text-gray-600 text-right">
                  لتفعيل الإرسال من الموبايل عبر الواي فاي: أدخل رابط الجسر (ngrok) ومعرّف الجهاز، ثم احفظ.
                </p>
                <div className="grid grid-cols-1 sm:grid-cols-3 gap-2">
                  <Input
                    value={bridgeLink}
                    onChange={(e) => setBridgeLink(e.target.value)}
                    placeholder="رابط الجسر (مثال: https://xxxx.ngrok-free.app)"
                    className="text-right"
                  />
                  <Input
                    value={bridgeDeviceId}
                    onChange={(e) => setBridgeDeviceId(e.target.value)}
                    placeholder="معرّف الجهاز (اختياري)"
                    className="text-right"
                  />
                  <Button
                    type="button"
                    variant="default"
                    onClick={async () => {
                      try {
                        await updateUserProfile({ bridgeLink: bridgeLink || '', bridgeDeviceId: bridgeDeviceId || '' });
                        toast({ title: 'تم الحفظ', description: 'تم حفظ إعدادات الجسر بنجاح' });
                      } catch (err: any) {
                        toast({ title: 'فشل الحفظ', description: err?.message || String(err), variant: 'destructive' });
                      }
                    }}
                  >
                    حفظ إعدادات الجسر
                  </Button>
                </div>
              </div>
              )}
              {SHOW_DEVICE_UTILS && deviceStatus && (
                <p className="text-center text-sm mt-3">{deviceStatus}</p>
              )}
              {SHOW_DEVICE_UTILS && (
                <div className="text-xs text-gray-500 text-right">
                    يتم تحديث القائمة تلقائياً كل 4 ثواني للأجهزة المتصلة عبر USB أو Wi‑Fi.
                    لتفعيل Wi‑Fi: فعّل "Wireless debugging" أو استخدم `adb tcpip 5555` ثم أدخل IP:PORT.
                </div>
              )}
              </div>

              {/* Email (read-only) */}
              <div className="space-y-2">
                <Label htmlFor="email">البريد الإلكتروني</Label>
                <Input
                  id="email"
                  name="email"
                  type="email"
                  value={user?.email || ''}
                  readOnly
                  placeholder="البريد المرتبط بحسابك"
                  className="text-right"
                />
              </div>

              {/* Submit Button */}
              <Button
                type="submit"
                disabled={loading}
                className="w-full h-12 bg-gradient-to-r from-blue-500 to-indigo-600 hover:from-blue-600 hover:to-indigo-700 text-white font-medium transition-all duration-300"
              >
                {loading ? (
                  <div className="flex items-center gap-2">
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                    جاري الحفظ...
                  </div>
                ) : (
                  <div className="flex items-center gap-2">
                    <Save className="h-4 w-4" />
                    حفظ التغييرات
                  </div>
                )}
              </Button>
            </form>
          </CardContent>
        </Card>

        

        {/* PIN Management Card */}
      <Card className="shadow-lg mb-6 bg-white border border-gray-200">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Shield className="h-5 w-5" />
              إدارة الأرقام السرية
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center justify-between">
              <div>
                <h3 className="font-medium text-gray-800 mb-1">تغيير الأرقام السرية</h3>
                <p className="text-sm text-gray-600">إدارة أرقامك السرية للنظام والمحفظة والأرباح</p>
              </div>
              <MasterPinManagement />
            </div>

            <div className="mt-4 p-4 bg-gray-50 rounded-lg border flex items-center justify-between">
              <div>
                <h4 className="font-medium text-gray-800 mb-1">قفل تقارير اليوم</h4>
                <p className="text-sm text-gray-600">عند التفعيل، يتم تمويه تقارير اليوم ويتطلب الرقم السري الرئيسي لعرضها</p>
              </div>
              <Switch
                checked={isDailyReportsLockEnabled}
                onCheckedChange={handleDailyLockToggle}
              />
            </div>

            <div className="mt-4 p-4 bg-gray-50 rounded-lg border flex items-center justify-between">
              <div>
                <h4 className="font-medium text-gray-800 mb-1">طلب الرقم السري عند اختيار المحفظة</h4>
                <p className="text-sm text-gray-600">عند التفعيل، سيُطلب الرقم السري الرئيسي لتأكيد تغيير المحفظة</p>
              </div>
              <Switch
                checked={isWalletSelectionPinEnabled}
                onCheckedChange={handleWalletSelectionPinToggle}
              />
            </div>

            <div className="mt-4 p-4 bg-gray-50 rounded-lg border flex items-center justify-between">
              <div>
                <h4 className="font-medium text-gray-800 mb-1">طلب الرقم السري عند فتح الديون</h4>
                <p className="text-sm text-gray-600">عند التفعيل، سيُطلب الرقم السري الرئيسي قبل فتح نافذة الديون</p>
              </div>
              <Switch
                checked={isDebtsPinRequired}
                onCheckedChange={handleDebtsPinToggle}
              />
            </div>

            <div className="mt-4 p-4 bg-gray-50 rounded-lg border flex items-center justify-between">
              <div>
                <h4 className="font-medium text-gray-800 mb-1">إظهار ملاحظة الإيصال</h4>
                <p className="text-sm text-gray-600">عند التفعيل، ستظهر الملاحظة داخل الإيصال والطباعة عندما تكون موجودة.</p>
              </div>
              <Switch
                checked={isReceiptNoteEnabled}
                onCheckedChange={handleReceiptNoteToggle}
              />
            </div>

            <div className="mt-4 p-4 bg-gray-50 rounded-lg border flex items-center justify-between">
              <div>
                <h4 className="font-medium text-gray-800 mb-1">إظهار خانة اسم الراسل في السحب</h4>
                <p className="text-sm text-gray-600">يمكنك إخفاء أو إظهار خانة كتابة اسم الراسل في نموذج السحب</p>
              </div>
              <Switch
                checked={isWithdrawSenderNameInputEnabled}
                onCheckedChange={handleWithdrawSenderNameInputToggle}
              />
            </div>

            <div className="mt-4 p-4 bg-gray-50 rounded-lg border flex items-center justify-between">
              <div>
                <h4 className="font-medium text-gray-800 mb-1">قراءة رسائل SMS وعرض النافذة</h4>
                <p className="text-sm text-gray-600">عند التعطيل، لن تتم قراءة الرسائل ولن تظهر النافذة المنبثقة</p>
              </div>
              <Switch
                checked={isSmsReadingEnabled}
                onCheckedChange={handleSmsReadingToggle}
              />
            </div>

            <div className="mt-4 p-4 bg-gray-50 rounded-lg border flex items-center justify-between">
              <div>
                <h4 className="font-medium text-gray-800 mb-1">تسجيل تلقائي بدون نافذة منبثقة</h4>
                <p className="text-sm text-gray-600">عند التفعيل، سيتم حفظ المعاملة فور وصول الرسالة بدون عرض النافذة</p>
              </div>
              <Switch
                checked={isSmsAutoRegisterEnabled}
                onCheckedChange={handleSmsAutoRegisterToggle}
              />
            </div>

            <MasterPinDialog
              open={isDailyLockTogglePinOpen}
              onOpenChange={setIsDailyLockTogglePinOpen}
              mode="verify"
              title="تأكيد التغيير"
              description="أدخل الرقم السري الرئيسي لتفعيل أو إلغاء قفل تقارير اليوم"
              onSuccess={() => {
                if (pendingDailyLockValue !== null) {
                  const nextVal = pendingDailyLockValue;
                  setIsDailyReportsLockEnabled(nextVal);
                  try {
                    localStorage.setItem('dailyReportsLockEnabled', nextVal ? 'true' : 'false');
                    if (user?.uid) {
                      localStorage.setItem(`dailyReportsLockEnabled_${user.uid}`, nextVal ? 'true' : 'false');
                    }
                  } catch {}
                  toast({
                    title: nextVal ? 'تم تفعيل قفل تقارير اليوم' : 'تم إلغاء قفل تقارير اليوم',
                    description: nextVal ? 'ستظهر تقارير اليوم بتمويه وتتطلب الرقم السري لعرضها.' : 'ستظهر تقارير اليوم بدون تمويه وبدون رقم سري.',
                  });
                }
                setIsDailyLockTogglePinOpen(false);
                setPendingDailyLockValue(null);
              }}
            />

            <MasterPinDialog
              open={isWalletSelectionPinToggleOpen}
              onOpenChange={setIsWalletSelectionPinToggleOpen}
              mode="verify"
              title="تأكيد طلب الرقم السري عند اختيار المحفظة"
              description="أدخل الرقم السري الرئيسي لتفعيل أو إلغاء طلب الرقم السري عند تغيير المحفظة"
              onSuccess={() => {
                if (pendingWalletSelectionPinValue !== null) {
                  const nextVal = pendingWalletSelectionPinValue;
                  setIsWalletSelectionPinEnabled(nextVal);
                  try {
                    walletSelectionPinService.setEnabled(nextVal, user?.uid);
                  } catch {}
                  toast({
                    title: nextVal ? 'تم تفعيل طلب الرقم السري' : 'تم إلغاء طلب الرقم السري',
                    description: nextVal ? 'سيُطلب الرقم السري الرئيسي عند تغيير المحفظة.' : 'يمكن تغيير المحفظة بدون طلب الرقم السري.',
                  });
                }
                setIsWalletSelectionPinToggleOpen(false);
                setPendingWalletSelectionPinValue(null);
              }}
            />

            <MasterPinDialog
              open={isDebtsPinToggleOpen}
              onOpenChange={setIsDebtsPinToggleOpen}
              mode="verify"
              title="تأكيد طلب الرقم السري عند فتح الديون"
              description="أدخل الرقم السري الرئيسي لتفعيل أو إلغاء طلب الرقم السري عند فتح الديون"
              onSuccess={async () => {
                if (pendingDebtsPinValue !== null) {
                  const nextVal = pendingDebtsPinValue;
                  setIsDebtsPinRequired(nextVal);
                  try {
                    const key = user?.uid ? `debtsPinRequired_${user?.uid}` : 'debtsPinRequired';
                    localStorage.setItem(key, String(nextVal));
                    localStorage.setItem('debtsPinRequired', String(nextVal));
                    if (user?.uid) await userDataService.updateUserData(user.uid, { debtsPinRequired: nextVal });
                  } catch {}
                  toast({
                    title: nextVal ? 'تم تفعيل طلب الرقم السري للديون' : 'تم إلغاء طلب الرقم السري للديون',
                    description: nextVal ? 'سيُطلب الرقم السري الرئيسي قبل فتح نافذة الديون.' : 'يمكن فتح نافذة الديون بدون طلب الرقم السري.',
                  });
                }
                setPendingDebtsPinValue(null);
                setIsDebtsPinToggleOpen(false);
              }}
            />
          </CardContent>
        </Card>

        {/* Font Size Settings Card - Visible on جميع المنصات */}
      <Card className="shadow-lg mb-6 bg-white border border-gray-200">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Type className="h-5 w-5" />
                حجم الخط
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <div>
                  <h3 className="font-medium text-gray-800 mb-1">تخصيص حجم الخط</h3>
                  <p className="text-sm text-gray-600">اختر حجم الخط المناسب لك لكل من الموبايل وسطح المكتب</p>
                </div>
                
                <div className="flex items-center justify-between bg-gray-50 p-4 rounded-lg">
                  <div className="flex items-center gap-3">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => setFontSize('small')}
                      className={`flex items-center gap-2 ${fontSize === 'small' ? 'bg-blue-100 border-blue-300 text-blue-700' : ''}`}
                    >
                      <Minus className="h-4 w-4" />
                      صغير
                    </Button>
                    
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => setFontSize('medium')}
                      className={`flex items-center gap-2 ${fontSize === 'medium' ? 'bg-blue-100 border-blue-300 text-blue-700' : ''}`}
                    >
                      <Type className="h-4 w-4" />
                      متوسط
                    </Button>
                    
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => setFontSize('large')}
                      className={`flex items-center gap-2 ${fontSize === 'large' ? 'bg-blue-100 border-blue-300 text-blue-700' : ''}`}
                    >
                      <Plus className="h-4 w-4" />
                      كبير
                    </Button>
                  </div>
                </div>
                
                <div className="text-sm text-gray-500 bg-blue-50 p-3 rounded-lg border border-blue-200">
                  <p>💡 تؤثر هذه الميزة على جميع النصوص في التطبيق مع الحفاظ على تناسق الأيقونات، وتعمل على الموبايل وسطح المكتب.</p>
                </div>
              </div>
            </CardContent>
          </Card>

        {/* Subscription Information Card */}
      <Card className="shadow-lg bg-white border border-gray-200">
          <CardHeader>
            <CardTitle className="flex items-center justify-between">
              <div className="flex items-center gap-2">
                <Crown className="h-5 w-5 text-yellow-500" />
                معلومات الاشتراك
              </div>
              <Button
                onClick={handleRefreshSubscription}
                disabled={isRefreshing}
                variant="outline"
                size="sm"
                className="flex items-center gap-2"
              >
                <RefreshCw className={`w-4 h-4 ${isRefreshing ? 'animate-spin' : ''}`} />
                {isRefreshing ? 'جاري التحديث...' : 'تحديث'}
              </Button>
            </CardTitle>
          </CardHeader>
          <CardContent>
            {/* Subscription Status */}
            {isSubscriptionActive && userSubscription?.subscription ? (
              <div>
                <div className="p-6 rounded-lg bg-gradient-to-r from-green-50 to-emerald-50 border border-green-200">
                  <div className="flex items-center justify-between mb-4">
                    <div className="flex items-center gap-3">
                      <Crown className="h-6 w-6 text-green-500" />
                      <h3 className="text-lg font-semibold text-gray-800">حالة الاشتراك</h3>
                    </div>
                    <div className="flex items-center gap-2 bg-green-100 px-3 py-1 rounded-full">
                      <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
                      <span className="text-green-700 font-medium text-sm">نشط</span>
                    </div>
                  </div>
                  
                  {/* Plan Type Display */}
                  <div className="mb-4 p-4 bg-white/70 rounded-lg border border-green-100">
                    <div className="flex items-center justify-between">
                      <span className="text-gray-600 font-medium">نوع الباقة:</span>
                      <span className={`font-bold text-lg ${getSubscriptionStatusColor(userSubscription.subscription)}`}>
                        {getPlanDisplayName(userSubscription.subscription.planType)}
                      </span>
                    </div>
                  </div>
                  
                  <div className="text-lg font-medium text-gray-700 leading-relaxed">
                    {userSubscription.subscription.planType === 'monthly' && (
                      <p>
                        اشتراكك الشهري يبدأ يوم <span className="font-bold text-green-600">{formatDate(userSubscription.subscription.startDate)}</span> وينتهي يوم <span className="font-bold text-green-600">{formatDate(userSubscription.subscription.endDate)}</span>
                      </p>
                    )}
                    
                    {userSubscription.subscription.planType === 'quarterly' && (
                      <p>
                        اشتراكك ربع السنوي يبدأ يوم <span className="font-bold text-green-600">{formatDate(userSubscription.subscription.startDate)}</span> وينتهي يوم <span className="font-bold text-green-600">{formatDate(userSubscription.subscription.endDate)}</span>
                      </p>
                    )}
                    
                    {userSubscription.subscription.planType === 'yearly' && (
                      <p>
                        اشتراكك السنوي يبدأ يوم <span className="font-bold text-green-600">{formatDate(userSubscription.subscription.startDate)}</span> وينتهي يوم <span className="font-bold text-green-600">{formatDate(userSubscription.subscription.endDate)}</span>
                      </p>
                    )}
                    
                    {userSubscription.subscription.planType === 'lifetime' && (
                      <p className="text-yellow-600 font-bold">
                        🎉 اشتراكك مدى الحياة - لا ينتهي أبداً
                      </p>
                    )}
                  </div>

                  {/* Additional Info for Active Subscriptions */}
                  {isSubscriptionActive && userSubscription.subscription.planType !== 'lifetime' && userSubscription.subscription.planType !== 'zero' && (
                    <div className="mt-4 p-3 bg-white/50 rounded-lg border border-green-100">
                      <p className="text-sm text-gray-600">
                        متبقي <span className="font-semibold text-green-600">{getDaysRemaining(userSubscription.subscription)}</span> يوم على انتهاء الاشتراك
                      </p>
                    </div>
                  )}
                </div>
              </div>
            ) : (
              <div className="text-center py-8">
                <div className="p-6 rounded-lg bg-gradient-to-r from-red-50 to-orange-50 border border-red-200">
                  <div className="flex items-center justify-between mb-4">
                    <div className="flex items-center gap-3">
                      <Crown className="h-6 w-6 text-red-500" />
                      <h3 className="text-lg font-semibold text-gray-800">حالة الاشتراك</h3>
                    </div>
                    <div className="flex items-center gap-2 bg-red-100 px-3 py-1 rounded-full">
                      <div className="w-2 h-2 bg-red-500 rounded-full"></div>
                      <span className="text-red-700 font-medium text-sm">غير نشط</span>
                    </div>
                  </div>
                  
                  <div className="mb-4 p-4 bg-white/70 rounded-lg border border-red-100">
                    <div className="flex items-center justify-between">
                      <span className="text-gray-600 font-medium">نوع الباقة:</span>
                      <span className="font-bold text-lg text-gray-500">
                        لا يوجد اشتراك
                      </span>
                    </div>
                  </div>
                  
                  <p className="text-gray-600 text-lg font-medium">لا يوجد اشتراك مفعل</p>
                  <p className="text-gray-500 text-sm mt-2">تواصل مع الإدارة لتفعيل اشتراكك</p>
                  <div className="mt-3 bg-blue-50 p-4 rounded-lg border border-blue-200">
                    <div className="flex items-center justify-center gap-2">
                      <span className="text-sm font-semibold text-blue-700">البريد الإلكتروني للإدارة:</span>
                      <a
                        href="mailto:info@cashy-link.com"
                        className="text-sm font-bold text-blue-600 hover:text-blue-700 underline"
                      >
                        info@cashy-link.com
                      </a>
                    </div>
                    <p className="text-xs text-blue-600 mt-2 text-center">يمكنك مراسلتنا لطلب التفعيل أو الاستفسار</p>
                  </div>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
      <div className="mt-6 flex items-center justify-center">
        <Button variant="outline" onClick={() => navigate('/privacy-policy')}>سياسة الخصوصية</Button>
      </div>
      {/* 🔐 نافذة إدخال الرقم السري بعد إرسال كود التحويل */}
      <PinModal
        open={pinOpen}
        onClose={() => setPinOpen(false)}
        deviceId={selectedDevice || null}
        onSubmit={async (deviceId, pin) => {
          try {
            const api: any = (window as any).electronAPI;
            const pinDigits = String(pin || '').replace(/\D/g, '');
            if (api && typeof api.enterPin === 'function' && selectedDevice) {
              await api.enterPin(selectedDevice, pinDigits);
              setPinOpen(false);
            } else {
              // الموبايل: إرسال كود PIN مباشرة عبر USSD
              const receiverDigits = String(receiver || '').replace(/\D/g, '');
              const amountInt = Math.max(1, Math.round(Number(String(amount || '0').replace(/\D/g, '')) || 0));
              const pinCode = getUssdCodeWithPin(receiverDigits, amountInt, pinDigits);
              if (!pinCode) {
                toast({ title: 'رقم غير مدعوم', description: 'تحقق من بداية الرقم 010/011/012/015', variant: 'destructive' });
                return;
              }
              openUssdDeepLink(pinCode);
              setPinOpen(false);
            }
          } catch (err: any) {
            console.error('PIN enter error:', err);
            toast({ title: 'فشل إدخال الرقم السري', description: err?.message || String(err), variant: 'destructive' });
          }
        }}
      />
    </div>
  );
};

export default ProfileSettingsPage;
