import React, { useState } from 'react';
import { avatarCollection } from '../utils/avatars';

interface AvatarSelectorProps {
  selectedAvatarId?: string;
  onAvatarSelect: (avatarId: string) => void;
  className?: string;
}

const AvatarSelector: React.FC<AvatarSelectorProps> = ({
  selectedAvatarId,
  onAvatarSelect,
  className = ''
}) => {
  const [isOpen, setIsOpen] = useState(false);

  const selectedAvatar = avatarCollection.find(avatar => avatar.id === selectedAvatarId);

  return (
    <div className={`relative ${className}`}>
      {/* زر عرض الصورة المختارة */}
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="w-20 h-20 rounded-full border-4 border-blue-500 hover:border-blue-600 transition-colors duration-200 bg-white shadow-lg overflow-hidden"
        type="button"
      >
        {selectedAvatar ? (
          <div className="w-full h-full">
            {selectedAvatar.component}
          </div>
        ) : (
          <div className="w-full h-full flex items-center justify-center bg-gray-100">
            <svg className="w-8 h-8 text-gray-400" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z" clipRule="evenodd" />
            </svg>
          </div>
        )}
      </button>

      {/* نص توضيحي */}
      <p className="text-sm text-gray-600 mt-2 text-center">
        اختر صورة البروفيل
      </p>

      {/* قائمة الصور المتاحة */}
      {isOpen && (
        <div className="absolute top-full left-1/2 transform -translate-x-1/2 mt-2 bg-white rounded-lg shadow-xl border border-gray-200 p-4 z-50 min-w-max">
          <div className="grid grid-cols-5 gap-3">
            {avatarCollection.map((avatar) => (
              <button
                key={avatar.id}
                onClick={() => {
                  onAvatarSelect(avatar.id);
                  setIsOpen(false);
                }}
                className={`w-16 h-16 rounded-full border-2 transition-all duration-200 hover:scale-105 overflow-hidden ${
                  selectedAvatarId === avatar.id
                    ? 'border-blue-500 ring-2 ring-blue-200'
                    : 'border-gray-300 hover:border-blue-400'
                }`}
                type="button"
                title={avatar.name}
              >
                <div className="w-full h-full">
                  {avatar.component}
                </div>
              </button>
            ))}
          </div>
          
          {/* زر الإغلاق */}
          <button
            onClick={() => setIsOpen(false)}
            className="mt-3 w-full py-2 text-sm text-gray-500 hover:text-gray-700 transition-colors"
            type="button"
          >
            إغلاق
          </button>
        </div>
      )}

      {/* خلفية شفافة للإغلاق عند النقر خارج القائمة */}
      {isOpen && (
        <div
          className="fixed inset-0 z-40"
          onClick={() => setIsOpen(false)}
        />
      )}
    </div>
  );
};

export default AvatarSelector;